/* Common styles for all 4-back formations */
.formation-4-1-2-1-2 .position-row.defenders,
.formation-4-3-2-1 .position-row.defenders,
.formation-4-2-1-3 .position-row.defenders,
.formation-4-4-2 .position-row.defenders,
.formation-4-3-3 .position-row.defenders {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  padding: 0 40px;
  z-index: 2;
}

/* Align wingers with fullbacks in 4-1-2-1-2 */
.formation-4-1-2-1-2 .position-row.wingMidfielders {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  padding: 0 40px;
  z-index: 2;
}

/* Align wingers with fullbacks in 4-3-3 */
.formation-4-3-3 .position-row.midfielders {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  padding: 0 40px;
  z-index: 2;
}

/* Align wingers with fullbacks in 4-4-2 */
.formation-4-4-2 .position-row.midfielders {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  padding: 0 40px;
  z-index: 2;
}

/* Align wingers with fullbacks in 4-2-1-3 */
.formation-4-2-1-3 .position-row.defensiveMidfielders {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  padding: 0 40px;
  z-index: 2;
}

/* Align wingers with fullbacks in 4-3-2-1 */
.formation-4-3-2-1 .position-row.centralMidfielders {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  padding: 0 40px;
  z-index: 2;
}