.challenge-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  background: #1a1a1a;
  color: #d4af37;
}

.challenge-content {
  flex: 1;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.football-decoration {
  position: absolute;
  top: 80px;
  right: 20px;
  width: 70px;
  height: 70px;
  background: 
    /* Base white ball */
    radial-gradient(circle at 50% 50%, 
      #ffffff 0%, 
      #f0f0f0 70%),
    /* Hexagon and pentagon pattern */
    repeating-conic-gradient(
      from 0deg at 50% 50%,
      #1a1a1a 0deg 36deg,
      transparent 36deg 72deg
    ),
    repeating-linear-gradient(
      60deg,
      transparent,
      transparent 10px,
      #1a1a1a 10px,
      #1a1a1a 11px,
      transparent 11px,
      transparent 20px
    );
  border: 2px solid #d4af37;
  border-radius: 50%;
  box-shadow: 
    0 0 10px rgba(212, 175, 55, 0.3),
    2px 2px 10px rgba(0, 0, 0, 0.5);
  transform: rotate(30deg);
  animation: float 4s ease-in-out infinite;
}

.football-decoration::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: 
    /* Additional pattern for realism */
    radial-gradient(
      circle at 30% 30%,
      rgba(26, 26, 26, 0.1) 5px,
      transparent 15px
    );
  border-radius: 50%;
}

@keyframes float {
  0%, 100% { transform: translateY(0) rotate(30deg); }
  50% { transform: translateY(-10px) rotate(30deg); }
}

/* Custom styling for Material-UI components */
.challenge-content .MuiTypography-root {
  color: #d4af37;
}

.challenge-content .MuiTextField-root {
  background: rgba(15, 15, 15, 0.1);
  border-radius: 4px;
}

.challenge-content .MuiInputBase-input {
  color: #d4af37;
}

.challenge-content .MuiOutlinedInput-notchedOutline {
  border-color: rgba(212, 175, 55, 0.3);
}

.challenge-content .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline {
  border-color: #d4af37;
}

.challenge-content .MuiButton-contained {
  background: linear-gradient(45deg, #d4af37 30%, #b8860b 90%);
  color: #0f0f0f;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s;
}

.challenge-content .MuiButton-contained:hover {
  transform: translateY(-2px);
  background: linear-gradient(45deg, #b8860b 30%, #d4af37 90%);
}

.challenge-content .MuiPaper-root {
  background: rgba(15, 15, 15, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(212, 175, 55, 0.2);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.challenge-content .MuiListItem-root {
  transition: background-color 0.2s;
}

.challenge-content .MuiListItem-root:hover {
  background: rgba(212, 175, 55, 0.1);
}